// Navigation
// ==========

@navigation-color:#fff;
@navigation-icon-color:@primary-color;
@navigation-icon-hover-color:darken(@primary-color, 30%);

@navigation-title-color: @primary-color;
@navigation-title-font-size: 18px;

@navigation-tooltip-font-size: 13px;

@navigation-border-width:1px;
@navigation-border-style:solid;
@navigation-border-color:@primary-color;
@navigation-border: @navigation-border-width @navigation-border-style @navigation-border-color;

@navigation-height: 60px;
@navigation-height-mobile: 50px;
@navigation-padding: ((@navigation-height - @icon-size)/2);
@navigation-padding-mobile: ((@navigation-height-mobile - @icon-size)/2);
@navigation-extra-padding-sides: 10px;


// Drawer
// ======

@drawer-color:@primary-color;
